<template>
  <div>
    <div class="container-fluid">
      <div class="row">
        <div class="col-lg">
          <img alt="Vue logo" src="scoring.png" width="400" height="275" />
          <div id="my_dataviz"></div>
        </div>
        <div class="col-lg">
          <canvas
            id="can"
            width="400"
            height="400"
            @mousedown="beginDrawing"
            @touchstart="beginDrawing"
            @mousemove="draw"
            @touchmove="draw"
            @mouseup="stopDrawing"
            @touchend="stopDrawing"
          >
          </canvas>
          <br />
          <button
            type="button"
            id="reset"
            class="btn btn-primary m-1"
            @click="reset"
          >
            Reset
          </button>
          <button
            type="button"
            v-if="!isEraser"
            id="eraser"
            class="btn btn-primary m-1"
            @click="toggleEraser"
          >
            Eraser
          </button>
          <button
            type="button"
            v-else
            id="eraser"
            class="btn btn-danger m-1"
            @click="toggleEraser"
          >
            Eraser
          </button>
          <button
            type="button"
            id="undo"
            class="btn btn-primary m-1"
            @click="undo"
          >
            Undo
          </button>
          <button
            type="button"
            id="redo"
            class="btn btn-primary m-1"
            @click="redo"
          >
            Redo
          </button>
          <button
            type="button"
            id="predict"
            class="btn btn-primary m-1"
            @click="predict"
          >
            Predict
          </button>

          <form method="post" accept-charset="utf-8" name="form1">
            <input name="data" id="data" type="hidden" />
          </form>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import * as d3 from "d3";

export default {
  name: "HelloWorld",
  data() {
    return {
      clicked: false,
      canvas: null,
      context: null,
      points: [],
      responseJSON: {},
      isEraser: false,
      rawPredictions: 0,
      history: {
        redo_list: [],
        undo_list: [],
        saveState: function(canvas, list, keep_redo) {
          keep_redo = keep_redo || false;
          if (!keep_redo) {
            this.redo_list = [];
          }

          (list || this.undo_list).push(canvas.toDataURL());
        },
        undo: function(canvas, cntx) {
          this.restoreState(canvas, cntx, this.undo_list, this.redo_list);
        },
        redo: function(canvas, cntx) {
          this.restoreState(canvas, cntx, this.redo_list, this.undo_list);
        },
        restoreState: function(canvas, cntx, pop, push) {
          if (pop.length) {
            this.saveState(canvas, push, true);
            var restore_state = pop.pop();
            // var img = new Element('img', {'src':restore_state});
            var img = document.createElement("img");
            img.src = restore_state;
            img.onload = function() {
              cntx.clearRect(0, 0, canvas.width, canvas.height);
              cntx.drawImage(
                img,
                0,
                0,
                canvas.width,
                canvas.height,
                0,
                0,
                canvas.width,
                canvas.height
              );
            };
          }
        }
      }
    };
  },
  props: {
    msg: String
  },
  methods: {
    sigmoid(t) {
      return 1 / (1 + Math.pow(Math.E, -t));
    },
    getMousePos(canvas, e) {
      var rect = canvas.getBoundingClientRect();
      return {
        x: e.clientX - rect.left,
        y: e.clientY - rect.top
      };
    },
    beginDrawing(e) {
      // console.log(e.pageX)
      // console.log(e.pageY)
      e.preventDefault();
      this.clicked = true;
      this.context.save();
      if (e.type == "mousedown") {
        var pos = this.getMousePos(this.canvas, e);
        var xCoord = pos.x;
        var yCoord = pos.y;
      } else if (e.type == "touchstart") {
        var touch = e.touches[0];
        // console.log(touch.clientX)
        var mouseEvent = new MouseEvent("mousedown", {
          clientX: touch.clientX,
          clientY: touch.clientY
        });
        this.beginDrawing(mouseEvent);
        return;
      }
      // var xCoord = e.pageX-this.canvas.offsetLeft;
      // var yCoord = e.pageY-this.canvas.offsetTop;
      this.history.saveState(this.canvas);
      this.points.length = 0;
      this.points.push({ x: xCoord, y: yCoord });
      this.context.moveTo(xCoord, yCoord);
    },
    draw(e) {
      // console.log(e.type)

      e.preventDefault();

      var pos = null;
      var xCoord = null;
      var yCoord = null;

      if (this.clicked == true && e.type == "mousemove") {
        // var xCoord= e.pageX- this.canvas.offsetLeft;
        // var yCoord= e.pageY- this.canvas.offsetTop;
        pos = this.getMousePos(this.canvas, e);
        xCoord = pos.x;
        yCoord = pos.y;

        this.points.push({ x: xCoord, y: yCoord });
        // console.log(xCoord)
      } else if (this.clicked == true && e.type == "touchmove") {
        var touch = e.touches[0];
        var mouseEvent = new MouseEvent("mousemove", {
          clientX: touch.clientX,
          clientY: touch.clientY
        });
        this.draw(mouseEvent);
        return;

        // pos = this.getMousePos(this.canvas, e)
        // xCoord = pos.x
        // yCoord = pos.y
        // xCoord=e.touches[0].pageX-this.canvas.offsetLeft;
        // yCoord=e.touches[0].pageY-this.canvas.offsetTop;

        // this.points.push({x: xCoord, y: yCoord})
      }
      // console.log("AFTER: ", points[points.length - 1])

      if (this.points.length >= 3) {
        const l = this.points.length - 1;
        const xc = (this.points[l].x + this.points[l - 1].x) / 2;
        const yc = (this.points[l].y + this.points[l - 1].y) / 2;
        // cntx.lineWidth = points[l - 1].lineWidth
        // console.log(xc, yc)
        this.context.quadraticCurveTo(
          this.points[l - 1].x,
          this.points[l - 1].y,
          xc,
          yc
        );
        this.context.stroke();
        this.context.beginPath();
        this.context.moveTo(xc, yc);
      }
    },
    stopDrawing(e) {
      e.preventDefault();

      var pos = this.getMousePos(this.canvas, e);
      var xCoord = pos.x;
      var yCoord = pos.y;

      if (this.clicked == true && e.type == "mouseup") {
        // xCoord=e.pageX-this.offsetLeft;
        // yCoord=e.pageY-this.offsetTop;
        pos = this.getMousePos(this.canvas, e);
        xCoord = pos.x;
        yCoord = pos.y;

        this.points.push({ x: xCoord, y: yCoord });
      } else if (
        this.clicked == true &&
        (e.type == "touchend" || e.type == "touchleave")
      ) {
        // xCoord=e.pageX-this.offsetLeft;
        // yCoord=e.pageY-this.offsetTop;
        var touch = e.changedTouches[0];
        var mouseEvent = new MouseEvent("mousemove", {
          clientX: touch.clientX,
          clientY: touch.clientY
        });
        this.draw(mouseEvent);
        // this.draw(e);
        return;
      }

      if (this.points.length >= 3) {
        const l = this.points.length - 1;
        this.context.quadraticCurveTo(
          this.points[l].x,
          this.points[l].y,
          xCoord,
          yCoord
        );
        this.context.stroke();
      }

      this.points.length = 0;

      // updatePredictions()
      this.clicked = false;
    },
    reset() {
      this.context.strokeStyle = "black";
      this.context.lineWidth = 3;
      this.context.lineCap = "round";
      this.context.fillStyle = "#fff";
      this.context.fillRect(0, 0, this.canvas.width, this.canvas.height);
    },
    toggleEraser() {
      this.isEraser = !this.isEraser;

      if (this.isEraser) {
        this.context.strokeStyle = "#fff";
        this.context.lineWidth = 10;
      } else {
        this.context.strokeStyle = "black";
        this.context.lineWidth = 3;
      }
    },
    predict() {
      console.log("in predict");
      var dataURL = this.canvas.toDataURL("image/png");

      document.getElementById("data").value = dataURL;
      var fd = new FormData(document.forms["form1"]);

      var xhr = new XMLHttpRequest({ mozSystem: true });
      xhr.open("POST", `http://${window.location.host}/predict`, true);
      // xhr.responseType = 'json';

      var ref = this;

      xhr.onreadystatechange = function() {
        if (xhr.readyState == XMLHttpRequest.DONE) {
          // document.getElementById('num').innerHTML = xhr.responseText;
          var responseText = JSON.parse(xhr.responseText);
          for (const [key, value] of Object.entries(responseText)) {
            console.log(`${key}: ${value}`);
            // targetProxy[key] = value
          }
          ref.rawPredictions = responseText;
          console.log(ref.rawPredictions);
          console.log(ref.processedPredictions);
        }
      };

      xhr.onload = function() {};
      xhr.send(fd);
    },
    undo() {
      this.history.undo(this.canvas, this.context);
    },
    redo() {
      this.history.redo(this.canvas, this.context);
    },
    setupCanvas(canvas) {
      // Get the device pixel ratio, falling back to 1.
      var dpr = window.devicePixelRatio || 1;
      // Get the size of the canvas in CSS pixels.
      var rect = canvas.getBoundingClientRect();
      // Give the canvas pixel dimensions of their CSS
      // size * the device pixel ratio.
      canvas.width = rect.width * dpr;
      canvas.height = rect.height * dpr;
      var ctx = canvas.getContext("2d");
      // Scale all drawing operations by the dpr, so you
      // don't have to worry about the difference.
      ctx.scale(dpr, dpr);
      console.log(ctx);
      canvas.width = rect.width;
      canvas.height = rect.height;

      return ctx;
    }
  },
  computed: {
    processedPredictions() {
      console.log(this.rawPredictions);
      if (this.rawPredictions == null) {
        return null;
      } else {
        var preds = [];
        for (const [key, value] of Object.entries(this.rawPredictions)) {
          console.log(`${key}: ${value}`);
          // targetProxy[key] = value
          preds.push({
            index: key,
            name: value[0],
            prob: value[1]
          });
        }
        console.log(preds);
        return preds;
      }
    }
  },
  watch: {
    rawPredictions() {
      var margin = { top: 10, right: 10, bottom: 30, left: 30 },
        width = 300 - margin.left - margin.right,
        height = 250 - margin.top - margin.bottom;

      d3.select("svg").remove();

      // append the svg object to the body of the page
      var svg = d3
        .select("#my_dataviz")
        .append("svg")
        .attr("width", width + margin.left + margin.right)
        .attr("height", height + margin.top + margin.bottom)
        .append("g")
        .attr("transform", "translate(" + margin.left + "," + margin.top + ")");

      var x = d3
        .scaleBand()
        .range([0, width])
        .domain(
          this.processedPredictions.map(function(d) {
            return d.index;
          })
        )
        .padding(0.2);
      svg
        .append("g")
        .attr("transform", "translate(0," + height + ")")
        .call(d3.axisBottom(x))
        .selectAll("text")
        .attr("transform", "translate(-10,0)rotate(-45)")
        .style("text-anchor", "end");

      // Add Y axis
      var y = d3
        .scaleLinear()
        .domain([0, 1])
        .range([height, 0]);
      svg.append("g").call(d3.axisLeft(y));

      // Bars
      svg
        .selectAll("mybar")
        .data(this.processedPredictions)
        .enter()
        .append("rect")
        .attr("x", function(d) {
          return x(d.index);
        })
        .attr("y", function(d) {
          return y(d.prob);
        })
        .attr("width", x.bandwidth())
        .attr("height", function(d) {
          return height - y(d.prob);
        })
        .attr("fill", "#69b3a2");
    }
  },
  mounted() {
    this.canvas = document.getElementById("can");

    this.context = this.setupCanvas(document.getElementById("can"));

    this.context.strokeStyle = "black";
    this.context.lineWidth = 3;
    this.context.lineCap = "round";
    this.context.fillStyle = "#fff";
    this.context.fillRect(0, 0, this.canvas.width, this.canvas.height);

    console.log(this.canvas.width);
  }
};
</script>

